<template>
  <ion-header>
    <ion-toolbar>
      <ion-buttons slot="start">
        <ion-back-button  defaultHref="/tabs/tab1" ></ion-back-button>
      </ion-buttons>
      <ion-title>Home</ion-title>
    </ion-toolbar>
  </ion-header>
  <in-app-browser></in-app-browser>
</template>

<script>
import { InAppBrowser, InAppBrowserObject } from "@ionic-native/in-app-browser";

function beforeloadCallBack(params, callback) {
  console.log(">>> beforeload: " + params.url.toString());

  if (params.url.includes("https://hust.media/")) {
    console.log(">>> beforeload: allowed");
    callback(params.url);
  } else {
    console.log(">>> beforeload: restricted");
    alert("The URL is restricted!");
  }
}

export default {
  components: {
    InAppBrowser,
  },
  setup() {
    let browser = InAppBrowserObject ;
    const options  = {
      location: "no",
      usewkwebview: "yes"
    };
    
     browser = InAppBrowser.create(
      'https://hust.media?=app',
      '_blank',
      options
    );

   browser.on("loadstop").subscribe((event) => {
    
    // alert('loadstop', event)
      console.log(">>> onLoadStop:" + event.url.toString());
    });
   browser.on("loadstart").subscribe((event) => {

    browser.show()
      console.log(">>> onLoadStart:" + event.url.toString());
    });
  browser.on("beforeload").subscribe((params) =>
      beforeloadCallBack(params, () => {
        return params.url;
      })
    );
  },
};
</script>